//
// Search
// --------------------------------------------------



.custom-search{
    position:relative;
}

.custom-search input{
    border-top-right-radius: 4px !important;
    border-bottom-right-radius: 4px !important;
    padding-right: 40px;
}

.custom-search .input-group-btn{
    position: absolute;
    right: 0;
    width: auto;
    top:1px;
}

.custom-search button {
    border: 0;
    background: none;
}


.custom-search button:hover,
.custom-search button:focus,
.custom-search button:active{
    background: none !important;
}

.custom-search .btn-success{
    color: @brand-success;
}

.custom-search .btn-warning{
    color: @brand-warning;
}

.custom-search .btn-danger{
    color: @brand-danger;
}

