//
// Header & Footer
// --------------------------------------------------


// Header
.app-header{
    background-color: @navbar-header-bg;
    color: @navbar-header-color;
    height: @navbar-header-height;
    top: 0;
    width: 100%;
    z-index: 999;
}


.app-name {
    font-size: 20px;    
    line-height:  @navbar-header-height;
    float: left;
    position: relative;
    margin: 0;
    font-weight: @headings-font-weight;
}


.app-body{
    border: none;
    margin: 0;
    padding: 0;
}